

<template>
  <v-app>
    <v-navigation-drawer 
      absolute
     class="hidden-sm-and-up"
      v-model="sideNav">
    
      <v-toolbar flat>
        <v-list>
          <v-list-tile>
            <v-list-tile-title class="title">
              {{ appName }}
            </v-list-tile-title>
          </v-list-tile>
        </v-list> 
      </v-toolbar>
      <v-list>
        <v-list-tile 
          v-for="item in menuItems" 
          :key="item.title"
          :to="item.link"
          >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-badge color="red" v-if="item.badge">
            <span slot="badge">{{ item.badge }}</span>
            <span>{{ item.title }}</span>
          </v-badge>
          <v-list-tile-content v-else>{{ item.title }}</v-list-tile-content>
        </v-list-tile>
      </v-list>
   </v-navigation-drawer>
    <v-toolbar>
      <v-toolbar-side-icon 
        class="hidden-sm-and-up"
        @click.stop="sideNav = !sideNav"
        >
      </v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link 
          to="/"
          tag="span"
          style="cursor: pointer"
          >
          {{ appName }}
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn flat 
          v-for="item in menuItems" 
          :key="item.title"
          :to="item.link"
          >
          <v-icon left>{{ item.icon }}</v-icon>
          <v-badge color="red" v-if="item.badge">
            <span slot="badge">{{ item.badge }}</span>
            <span>{{ item.title }}</span>
          </v-badge>
          <span v-else>{{ item.title }}</span>
        </v-btn>
      </v-toolbar-items>  
    </v-toolbar> 
  
    <div v-if="mode">
      <router-view v-on:show-log="printText"></router-view>
    </div>
    <div v-else>
      <div id="graph"></div>      
    </div>    
  </v-app>     
</template>

 
<script>
  export default {
   
    data () {
       
       return {
         mode : true,
         sideNav: true,
         appName : "SPES",
         menuItems: [
                {icon: 'date_range', title: 'ALA', link: '/calendar'},
                //{icon: 'person', title: 'Profile', link: '/profile'},
                {icon: 'notifications', title: 'SCM', badge:1, link: '/notification'},
                {icon: 'notifications', title:'SCR', badge:0, link:'/graph'},
                {icon: 'notifications', title:'FILE', badge:0, link:'/file'},

          ]    
       }  
    
    },
    methods : {
      printText(variable) {
  
      //  return ( this.sidNav = variable )
      this.mode = variable
 
           
      }

    }
  }
</script>
  
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
