
<template>
<v-layout>
    <v-container>
      <v-flex>
        
      {{ propsdata }}
      ============================
      </v-flex> 
      <v-flex>
        <div ref="graph" class="vue-plotly"/>
      </v-flex>
    </v-container>>
  </v-layout> 
</template>
<script>
 
import Plotly from 'plotly.js-dist'; 
import axios from 'axios'

import testGraph from "./AppScmTestGraph"

 export default{
   props : ['propsdata']
   ,
   data () {
     return {
       polling : null,
       sessionList : [],
       cpuUsage : [],
       startTime : '',
       totCpuInfo : new Map()
     }
   },
   mounted() {
    this.createGraph()
    
    this.startInterval()
 
  },
  components : {
      testGraph
  },  
  methods: {

    leadingZero(value) {

      if (value < 10) {
        return "0" + value.toString();
      }
      return value.toString();
    },
    sortInfo(){
      
      for(var i=0; i<this.propsdata.length; i++)
      {
        this.totCpuInfo.set(  this.propsdata[i].serverGroup+'_'+
                              this.propsdata[i].serverName+'_'+
                              this.propsdata[i].serverIp,100
        )
      }
    }
    ,
    createGraph(){
      axios.post('http://localhost:8081/postData',this.propsdata,
        {
          headers : {'Content-Type' : 'application/json'}
        }
      )
      .then(response => {
        this.result = response.data
        this.sessionList = response.data

      }).catch((ex) => {
        console.warn("ERROR : " , ex)
      })
      this.startTime = new Date()
      var yAxisList = []
      var time = this.leadingZero(this.startTime.getFullYear())
               + "."
               + (this.startTime.getMonth() + 1)
               + "."
               + this.leadingZero(this.startTime.getHours()%12)
               + ":"
               + this.leadingZero(this.startTime.getMinutes())
               + ":"
               + this.leadingZero(this.startTime.getSeconds())

      this.totCpuInfo.forEach(function(value, key, map){
                
          yAxisList.push( { x:[time] , y:[0], type : 'line', name : key.split("_")[1]})
      
        
      })

      Plotly.newPlot(this.$refs.graph,
          yAxisList
        
        
      );

    }
    ,
    startInterval : function(){
        this.polling = setInterval(() =>{
          var timecheck = new Date()

          if(this.isStarted)
          {
            this.getData()
            Plotly.extendTraces(this.$refs.graph, this.makeGraphData(), this.makeGraphCountArrayData())
          }
        },5000)
      }
    ,
    getData()
    {
        axios.post('http://localhost:8081/grapData', this.propsdata,
        {
          headers : {'Content-Type' : 'application/json' }
        }
        ).then(response => {
          this.result = response.data
          this.cpuUsage = response.data
        }).catch((ex) =>{
          console.warn("ERROR : " , ex)
        })


        Object.keys(this.cpuUsage).forEach( key => {

          var tempKey = key;
          if(tempKey == "_c")
          {

          }

          this.totCpuInfo.set(tempKey, this.cpuUsage[tempKey]);





        })

        return this.totCpuInfo

    }
    ,
    makeGraphData()
    {
      var yData = []
      var xData = []

      this.startTime.setSeconds(this.startTime.getSeconds()+5)
      var time = this.leadingZero(this.startTime.getFullYear())
               + "."
               + (this.startTime.getMonth() + 1)
               + "."
               + this.leadingZero(this.startTime.getHours()%12)
               + ":"
               + this.leadingZero(this.startTime.getMinutes())
               + ":"
               + this.leadingZero(this.startTime.getSeconds())

      this.totCpuInfo.forEach( function(value, key, map){

        var a=[]
        var b=[]

        if(value==-1)
        { 
          a.push(' ')
          b.push(time)
        }
        else
        {
          a.push(100-value)
          b.push(time)
        }

        yData.push(a)
        xData.push(b)
       })

        var last = { x: xData , y : yData}
        return last
    }
    ,
    makeGraphCountArrayData()
    {
      var yAxis = []
      var index = 0;
      this.totCpuInfo.forEach(function(value, key, map){
        yAxis.push(index)
        index++
      })

      index = 0

      return yAxis
    }
    ,
    check() {
      setTimeout(function() {
        console.log(' check Works!')
      }, 3000);
    }
 
  } 
  
 }
 
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>
