<template>
  <div>
   <scmInput v-on:addInfo="addInfo"></scmInput>
   <scmList v-bind:propsdata="infos"></scmList>
  </div>
</template>
<script>
import scmInput from './ScmInput.vue'
import scmList from './ScmList.vue'
import scmDialog from './ScmDialog.vue'

  export default {
    data: () => ({
      infos : []
    }),

    methods : {

      addInfo(info)
      {
        
        //console.log('server event received : ' +  info.serverGroup + ' ' + info.serverName + ' '  + info.serverIp)        
       // console.log(JSON.stringify(info))
        this.infos.push(JSON.parse(JSON.stringify(info)))
        console.log(info)
        
       
        //console.log('infos : ' + this.infos)
        

      }
    }
    ,
    components : {
      scmInput,
      scmList
    }
  }
</script>