<template>
<div>
  <v-form ref="form" v-model="valid">
    <v-container class="my-1 mx-5">
      <v-layout align-center>
        <v-flex>
           <v-select
            v-model="serverGroup"
           :items="items"          
            label="Group"
            required
          ></v-select>
        </v-flex>

        <v-flex>
          <v-text-field
            v-model.trim="serverName"
            :rules="InputDataRules"
            label="Server Name"
            required
          ></v-text-field>
        </v-flex>

        <v-flex>
          <v-text-field
            v-model="serverIp"
            :rules="serverIpRule"
            label="Server Ip"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverUser"
            :rules="InputDataRules"
            label="Server User"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverPassword"
            :rules="InputDataRules"
            label="Server Password"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverPort"
            :rules="portRules"
            label="Server Port"
            required
          ></v-text-field>
        </v-flex>                        
        <v-flex>
          <v-btn icon mx-0 >
            <v-icon v-on:click="addInfo()">done</v-icon>
          </v-btn>      
        </v-flex>
      </v-layout>
    </v-container>
  </v-form>
</div>  
</template>
<script>
  export default {
    data: () => ({
      valid: false,
      serverGroup : '',
      serverName : '',
      serverIp : '',
      serverUser : '',
      serverPassword : '',
      serverPort : '',      
      InputDataRules: [
      v => !!v || 'Input is required',
     // v => v.length >= 3 || 'Input Data must be more than 3 characters'
      ],
      portRules: [
      v => !!v || 'Input is required',
      v =>/^()([1-9]|[1-5]?[0-9]{2,4}|6[1-4][0-9]{3}|65[1-4][0-9]{2}|655[1-2][0-9]|6553[1-5])$/.test(v) || 'IP port number must be between 1 and 65535' 
      ],      
      serverIpRule: [
         v => !!v || 'Ip Input is required',         
         v => /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(v) || 'Server IP must be valid'
      ]
      ,
      info : {serverGroup : '', serverName : '', serverIp : '', serverUser : '', serverPassword : '', serverPort : ''}
      ,
      items: [
      'WEB',
      'WAS',
      'DB',
      'EAI',
      'ETC'
    ],
    })
    ,
    methods :
    {
      addInfo () {
        console.log(this.valid)
          if(this.valid)
          {
            this.info.serverGroup = this.serverGroup;
            this.info.serverName = this.serverName
            this.info.serverIp = this.serverIp
            this.info.serverUser = this.serverUser;
            this.info.serverPassword = this.serverPassword
            this.info.serverPort = this.serverPort
            this.serverGroup = ''
            this.serverName  =''
            this.serverIp = ''
            this.serverPassword = ''
            this.serverUser  =''
            this.serverPort = ''        
            this.$emit('addInfo',this.info)
            this.valid = false;
            this.$refs.form.reset()
          }
    

      } 
    }
  }
</script>