                <v-icon v-on:click="deleteInfo(dataRow)">delete</v-icon>
              </v-btn>             
          </td>        
        </template>
      </v-data-table>
    </v-flex>
  <v-flex class="text-xs-right">
    <v-btn icon>
       <v-icon v-on:click="monitorStart()">launch </v-icon>
    </v-btn>
  </v-flex>
fsdfdsfdfds
</v-container>  
</div>
