<template>
    <div>
        <v-flex>
            <scmInput v-if=this.show v-on:addInfo="addInfo"></scmInput>
            <scmList  v-if=this.show v-bind:propsdata="infos" v-on:monitorStart="monitorStart"></scmList>
            <scmGraph v-if=!this.show v-bind:propsdata="infos" v-on:monitorEnd="monitorEnd"></scmGraph>
        </v-flex>
    </div>
</template>

<script>

import scmInput from './AppScmInput.vue'
import scmList from './AppScmList.vue'
import scmGraph from './AppScmGraph.vue'

export default {

    data : () => ({
        infos : [],
        show :true
    }),
    mounted() {
        console.log('AppScm vue is mounted')
    },
    created() {
        window.addEventListener('beforeunload',() => {
            this.check();
        },false)
    },
    destroyed() {
        console.log('AppScm.vue is destroyed')
    },
    beforeDestroy(){
        console.log('AppScm.vue is beforeDestroy')
    },
    methods : {
        addInfo(info)
        {
            this.infos.push(JSON.parse(JSON.stringify(info)))
            console.log(info)
        }
        ,
        monitorStart(serverListInfo)
        {
            var startTime = new Date()
            this.show = false

            this.infos=[] //AppScmList에서 삭제한 데이터가 있을 수 있기 때문에 AppScmList에서 확정된 데이터를 다시 infos에 저장

            for(var i=0; i<serverListInfo.length; i++)
            {
                serverListInfo[i].time = startTime 
            }

            this.infos = JSON.parse(JSON.stringify(serverListInfo))
            //시간추가

            console.log('revised Info : ' + JSON.stringify(this.infos))

        },
        monitorEnd()
        {
            this.show = true
        }
        ,
        check()
        {
            console.log('check is called')
        }
    }
    ,
    components : {
        scmInput,
        scmList,
        scmGraph
    }
}

</script>
