
<template>
  <v-layout>
    <v-container>
      <v-flex>
        dasdas
      {{ propsdata }}
      
      </v-flex> 
      <v-flex>
        <div ref="graphDb" class="vue-plotly"/>
      </v-flex>
    </v-container>>
  </v-layout> 
</template>
<script>
 
 import Plotly from 'plotly.js-dist';
 
 export default{ 
   props : ['propsdata'] 
   
   ,
   data () {
     return {
 
       startTime : ''
     }
   }
   ,
   watch: {
     
      propsdata : {

      handler: function(val) {
        console.log('handler');
              } 
              ,
              deep: true
    }
  }
   ,
   mounted() {
    
 
  },
  methods: {

    leadingZero(value) {

      if (value < 10) {
        return "0" + value.toString();
      }
      return value.toString();
    }
    ,
    startInterval: function () {

      setInterval(() => {
      
        this.startTime = new Date()
        this.startTime.setSeconds(this.startTime.getSeconds() + 5)
        var time = this.leadingZero(this.startTime.getFullYear())
               + "."
               + (this.startTime.getMonth() + 1) 
               + "."
               + this.startTime.getDate()
               + " " 
               + this.leadingZero(this.startTime.getHours()%12)
               + ":" 
               + this.leadingZero(this.startTime.getMinutes())
               + ":" 
               + this.leadingZero(this.startTime.getSeconds())
        Plotly.extendTraces(this.$refs.graphDb,{ x:[[time],[time]], y:[[Math.random()*100],[Math.random()*100]] } ,[0,1]);
     }, 5000)
    },
    test () {
      this.startTime = new Date();
      
    //  t.setSeconds(t.getSeconds() + 10);
      var time =  this.leadingZero(this.startTime.getFullYear())
               + "."
               + (this.startTime.getMonth() + 1)  
               + "."
               + this.startTime.getDate()
               + " "
               + this.leadingZero(this.startTime.getHours()%12)
               + ":" 
               + this.leadingZero(this.startTime.getMinutes())
               + ":" 
               + this.leadingZero(this.startTime.getSeconds())
      // console.log(typeof(time))
      // console.log(typeof(currentdate))
      Plotly.newPlot(this.$refs.graphDb, 
          [
            {  x:[time], y:[0], type:'line'},
            {  x:[time], y:[0], type:'line'},
          ]);
 
    } 
 
  } 
  
 }
 
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>
