 
<template>
<div>
  <label >
    <input type="file" @change="loadTextFromFile">
  </label>
</div>  
</template>
 <script>
export default {
  methods: {
    loadTextFromFile(ev) {
      const file = ev.target.files[0];
      const reader = new FileReader();

      reader.onload = e => this.$emit("load", e.target.result);
      reader.readAsText(file);
    }
  }
};
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>